<template>
  <div>
    <table class="pdf-table fs-12 w-100 pdf-table-freight mt-2 text-center">
      <thead>
        <tr class="">
          <th width="38">项号</th>
          <th>货号</th>
          <th>描述</th>
          <th>数量</th>
          <th>单价</th>
          <th width="38">折扣</th>
          <th>金额</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(v, i) in freights" :key="v.id">
          <td>{{ i + 1 }}</td>
          <td>{{ v.freight.num }}</td>
          <td>{{ v.freight.named }}</td>
          <td>
            <div style="width: 60%" class="text-right float-left">{{ v.num }}&nbsp;</div>
            <div style="width: 40%" class="text-left float-left">{{ unit(v.freight.unit) }}</div>
          </td>
          <td class="text-right">{{ v.freight.price }}</td>
          <td>&nbsp;</td>
          <td class="text-right">{{ (parseFloat(v.freight.price) * parseFloat(v.num)).toFixed(2) }}</td>
        </tr>

        <tr class="pdf-table-freight-footer br-t-pdf">
          <td>备注</td>
          <td colspan="3">
          </td>
          <td>项目合计</td>
          <td>HK$</td>
          <td class="text-right"></td>
        </tr>
        <tr class="pdf-table-freight-footer">
          <td></td>
          <td colspan="3">
          </td>
          <td>附加费</td>
          <td></td>
          <td class="text-right"></td>
        </tr>
        <tr class="pdf-table-freight-footer">
          <td></td>
          <td colspan="3">
          </td>
          <td>折扣</td>
          <td></td>
          <td class="text-right"></td>
        </tr>
        <tr class="pdf-table-freight-footer">
          <td></td>
          <td colspan="3">
          </td>
          <td>统计</td>
          <td>HK$</td>
          <td class="text-right">{{ result_price }}</td>
        </tr>

      </tbody>
    </table>
  </div>
</template>

<script>
import util from '@/common/es/view'
import tool from '@/common/es/tool'
export default {
  name: 'InvoiceFreightTable',
  props: {
    freights: {
      type: Array
    }
  },
  computed: {
    freight_price() {

    },
    result_price() {
      let res = 0.00
      for (const v of this.freights) {
        const freight_price = v.freight.price
        const freight_num = v.num
        const sumR = tool.floatNum.floatMul(freight_price, freight_num)
        res = tool.floatNum.floatAdd(sumR, res)
      }
      return res
    }
  },
  methods: {
    unit(u) {
      return util.ser_unit(u)
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.pdf-table-freight
  border: 1px solid #5a5a5a

.pdf-table-freight thead th
  padding: 6px $distance
  border: 1px solid #5a5a5a

.pdf-table-freight td
  border-left: 1px solid #5a5a5a

.pdf-table-freight-footer
  border: none

.pdf-table-freight-footer > td::nth-child(1) 
  border: none

.br-t-pdf
  border-top: 1px solid #5a5a5a
</style>


