<template>
  <div class="data-panel def-shadow bg-white">

    <div class="row">
      <div class="col-sm-5 pt-2">
        <listing-plus-member :listing_num="listing_numed"></listing-plus-member>
      </div>
      <div class="col-sm-7">
        <listing-plus-event @invoiceViewSubmit="invoiceViewModal"></listing-plus-event>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-12">
        <table class="table mt-3">
          <thead>
            <tr>
              <th scope="col" width="90px" class="text-center">序号</th>
              <th scope="col" class="text-center">货号</th>
              <th scope="col">描述</th>
              <th scope="col">数量</th>
              <th scope="col">单价</th>
              <th scope="col">金额</th>
              <th scope="col" class="text-center">选择</th>
            </tr>
          </thead>
          <tbody class="listing-tbody">
            <tr v-for="(l, index) in listings" :key="l.id" :class="{ inActive: !inList(l.id, select) }">
              
              <th scope="row" class="text-center text-main">{{ index + 1 }}</th>
              <td class="text-center">{{ l.freight.num }}</td>
              <td>{{ l.freight.named }}</td>

              <td>
                <div class="input-group input-group-sm float-left listing-num-input">
                  <input type="text" class="form-control" :id="'num_' + l.id" :value="l.num" @keyup.enter="changeNum(l.id, index)">
                  <div class="input-group-append">
                    <span class="input-group-text" id="basic-addon2">{{ unit(l.freight.unit) }}</span>
                  </div>
                </div>
              </td>

              <td class="text-body">$ {{ l.freight.price }}</td>
              <td>$ {{ (l.freight.price * l.num).toFixed(2) }}</td>

              <td class="text-center">
                <div v-show="inList(l.id, select)">
                  <!-- 已加 -->
                  <i class="fa hand listing-table-icon fa-remove" @click="itemOption(l.id, 'remove')"></i>
                </div>
                <div v-show="!inList(l.id, select)">
                  <!-- 未加 -->
                  <i class="fa hand listing-table-icon fa-plus" @click="itemOption(l.id, 'add')"></i>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <invoice-view :listings="listingSelect"></invoice-view>
  </div>
</template>

<script>
import $ from 'jquery'
import util from '@/common/es/view'
import tool from '@/common/es/tool'
import service from '@/service'
import InvoiceView from '../Invoice/InvoiceView.vue'
import ListingPlusMember from './Inner/ListingPlusMember.vue'
import ListingPlusEvent from './Inner/ListingPlusEvent.vue'

export default {
  components: { InvoiceView, ListingPlusMember, ListingPlusEvent },
  name: 'ListingPlusDataTable',
  data() {
    return {
      listing_numed: 'S000374',
      select: [ 1, 3 ],
      listings: service.listings,
      listingSelect: []
    }
  },
  methods: {
    unit(u) {
      return util.ser_unit(u)
    },
    inList(n, L) {
      return tool.inList(n, L)
    },
    removeItem(id) {
      const i = tool.indexList(id, this.select)
      if (this.select.length < 2) {
        tool.showToast('至少选择一项菜品。')
      } else {
        this.select.splice(i, 1)
      }
    },
    addItem(id) {
      const i = tool.indexList(id, this.select)
      if (i < 0) {
        this.select.push(id)
      }
    },
    itemOption(id, option) {
      if (option == 'add') {
        this.addItem(id)
      } 
      else if (option == 'remove') {
        this.removeItem(id)
      }
    },
    invoiceViewModal() {
      this.listingSelect = this.listings.filter((e) => {
        return this.inList(e.id, this.select)
      })

      tool.showModal('invoiceViewModal')
    },
    changeNum(id, index) {
      const val = $('#num_' + id).val()
      this.listings[index].num = val
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.fa-pos-fix-wrapper
  width: 30px
  height: 20px
  border: 1px solid red

.listing-table-icon
  color: #17a2b8

.listing-tbody
  color: #17a2b8

.listing-num-input
  width: calc( 5vw + 40px )
  border-radius: $horizonS
  overflow: hidden
  background: none !important
  border-radius: 1px !important
  border: none !important

.listing-num-input .input-group-text
  border-top-right-radius: $horizonS
  border-bottom-right-radius: $horizonS
  border-left: none
  background: white
  border: none !important

.listing-num-input input
  border: none
  background: none !important
  border-radius: 1px !important


.inActive 
  background: #fafafa
  color: $accent
  
.inActive .listing-table-icon
  color: $scaffold

// .listing-num-input input, 
// .listing-num-input span
//   color: $primary !important

// .inActive input,
// .inActive span 
//   color: $input !important

</style>


