<template>
  <div>
    <div class="row">
      <div class="col-sm-9">
      </div>
      <div class="col-sm-3">
        <div class="btn btn-light btn-sm float-right btn-sub">
          <div class="d-inline" @click="saveTest">存为草稿</div>
        </div>
      </div>
    </div>
    <div class="row mt-3">
      <div class="col-sm-5"></div>
      <div class="col-sm-2">
        <!-- div class="form-goup form-goup-sm"> 
          <input type="text" class="form-control form-control-sm search-input" aria-label="freight-search">
        </div -->
      </div>
      <div class="col-sm-2">
        <!-- div class="btn btn-light btn-sm btn-block">
          <div class="d-inline" @click="saveTest">存为草稿</div>
        </div -->
      </div>
      <div class="col-sm-3">
        <div class="btn btn-outline-info btn-sm btn-block">
          <div class="d-inline" @click="openInvioceView">转换为发票</div>
        </div>
      </div>
    </div>
    <div class="row mt-3">
      <div class="col-lg-7 col-sm-2"></div>
      <div class="col-lg-5 col-sm-10">
        <div class="float-left d-inline">报价日期</div>
        <div class="float-right d-inline">{{ time_listing }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import util from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'ListingPlusEvent',
  data() {
    return {
      time_listing: '2020/11/16',
    }
  },
  methods: {
    openInvioceView() {
      this.$emit('invoiceViewSubmit')
    },
    saveTest() {
      tool.showToast('无响应。')
    }
  }
} 
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.btn-sub
  
</style>


