<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="invoiceViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <!-- div class="modal-header">
          <h5 class="modal-title">Modal title</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div -->
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <div class="pdf-title">{{ pdf_title }}</div>
              <div>
                {{ pdf_sub }}
              </div>
              <div>
                电话：{{ pdf_contact_phoned }}&nbsp;&nbsp;传真：{{ pdf_contact_fax }}
              </div>
              <div>
                电邮地址：
              </div>
              <div class="pdf-title mt-2">
                发票
              </div>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="70%">
                    <invoice-member-table :member="memberSelect"></invoice-member-table>
                  </td>
                  <td>
                    <invoice-invoice-table></invoice-invoice-table>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <invoice-freight-table :freights="listings"></invoice-freight-table>
            </div>

          </div>
          
          <invoice-footer :member="memberSelect"></invoice-footer>
        </div>
        <div class="modal-footer">
          
          <button class="btn btn-outline-info btn-sm" @click="invoicePrint">导出成 Pdf</button>
            
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import service from '@/service'
import tool from '@/common/es/tool'
import InvoiceFooter from './Table/InvoiceFooter.vue'
import InvoiceFreightTable from './Table/InvoiceFreightTable.vue'
import InvoiceInvoiceTable from './Table/InvoiceInvoiceTable.vue'
import InvoiceMemberTable from './Table/InvoiceMemberTable.vue'

export default {
  components: { InvoiceFreightTable, InvoiceMemberTable, InvoiceInvoiceTable, InvoiceFooter },
  name: 'InvoiceView',
  props: {
    listings: {
      type: Array
    }
  },
  data() {
    return {
      pdf_title: 'Lumicrest International Limited',
      pdf_sub: 'Office: Rm A12, 8/F Lee King Industrial Building 12 Ng Fong Street, San Po Kong, Kowloon, Hong',
      pdf_contact_phoned: '5488 1979 5424 4119',
      pdf_contact_fax: '2777 0760',
      memberSelect: {}
    }
  },
  created() {
    const m = tool.storage._get('memberSelect')
    this.memberSelect = JSON.parse(m)
  },
  methods: {
    invoicePrint() {
      // tool.go()
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.pdf-title
  font-size: $h4
  font-weight: bold


.pdf-table-outter
  width: 100%
  min-height: 49px

.pdf-table-outter td
  border: 1px solid #5a5a5a

</style>


