<template>
  <div>
    <div class="h4 overflow-hidden">
      {{ member.named }}
    </div>
    <div>
      {{ member.addr }}
    </div>
    
    <div class="row">
      <div class="col-lg-6 col-sm-10">
        <div class="mt-2">
          <div class="float-left d-inline">客户编号</div>
          <div class="float-right d-inline">{{ member.num }}</div>
        </div>
        <div>
          <div class="float-left d-inline">报价编号</div>
          <div class="float-right d-inline">{{ listing_num}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
export default {
  name: 'ListingPlusMember',
  props: {
    listing_num: {
      type: String
    }
  },
  data() {
    return {
      member: {
        named: '',
        num: '',
        addr: ''
      }
    }
  },
  created() {
    const m = this.$route.query.memberSelect
    console.log('m = ' + m)

    if (m != undefined) {
      this.member = this.$route.query.memberSelect
    } else {
      const res = tool.storage._get('memberSelect')
      this.member = JSON.parse(res)
    }
  },
  mounted() {
    const res = tool.storage._get('memberSelect')
    this.member = JSON.parse(res)
  }
}
</script>

<style lang="sass" scoped>

</style>


